import React from "react";
import { IDropableComponent } from "../../Interfaces/IDropableComponent";
import "./DroppableComponent.css";
interface IState {}
export default class DroppableComponent extends React.Component<
  IDropableComponent,
  IState
> {
  render() {
    return (
      <React.Fragment>
        <div
          className="droppable-component"
          onDragOver={(ev: React.DragEvent<HTMLDivElement>) =>
            this.props.onDragOver(ev)
          }
          onDrop={(ev: React.DragEvent<HTMLDivElement>) =>
            this.props.onDrop(ev, this.props.name)
          }
          data-id={this.props.name}
        >
          <span>Drop components here!</span>
          {this.props.children}
        </div>
      </React.Fragment>
    );
  }
}
